import validate from '../../common/ys-validate.js'
export default {
	data() {
		return {
			auth_time: 60,
			getCodeText: '60s后重新获取',
			getOver: true,
			code:'',
			phone:'',
			newPsd:'',
			type:0,
			typeText:["修改手机号","修改密码"]
		}
	},
	methods: {
		navTo(url) {
			this.$commonJs.navTo(url)
		},
		getCode() {
			var that = this;
			this.$apiJs.getCode({
				phone:this.$store.state.userInfo.phone,
			}).then(res => {
				if(res){
					that.$commonJs.uniMsg('发送成功', 'success')
					this.timeOut();
				}
			})
		},
		timeOut(){
			var that = this;
			var auth_timetimer = setInterval(() => {
				that.auth_time--;
				that.getCodeText = this.auth_time + 's后重新获取';
				that.getOver = true;
				if (that.auth_time <= 0) {
					that.getCodeText = '获取验证码';
					that.getOver = false;
					clearInterval(auth_timetimer);
				}
			}, 1000);
		},
		editPhone() {
			var that = this;
			var Rules = [{
					name: 'phone',
					type: 'required',
					errmsg: '请输入手机号'
				},
				{
					name: 'phone',
					type: 'phone',
					errmsg: '请输入正确的手机号'
				}
			];
			let valLoginRes = this.$validate.validate({
				'phone':this.phone
			}, Rules)
			if (!valLoginRes.isOk) {
				this.$commonJs.uniMsg(valLoginRes.errmsg)
				return false
			}
			
			this.$commonJs.navBack();
		},
		editPsd() {
			var that = this;
			this.$apiJs.upPwd({
				code:this.code,
				password:this.newPsd
			}).then(res=>{
				this.$commonJs.uniMsg('修改成功')
				this.$commonJs.navBack();
			})
		},

	},
	onLoad(option) {
		this.type = Number(option.type);
		uni.setNavigationBarTitle({
			title:this.typeText[this.type]
		})
		this.timeOut();
	}
}
